
import {Toast} from 'mint-ui'
import PicView from '../../components/picView'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
directives: {
TransferDom
},
name: 'photoAlbum',
components: {
PicView, XDialog
},
data() {
return {
showDialog: false,
list:[],
isShow: false,
imgData: []
}
},
mounted() {
this.getAlbumList();
},
methods: {
getMsgPic(data) {
this.showDialog = data
},
showImgList(list){
this.imgData = list;
this.showDialog=true
},
getAlbumList(){
let par = {
shop_id: this.$route.query.shop_id
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/detail/album`, par).then(res => {
if (res.data.code === 200) {
this.list = res.data.data
this.isShow = !this.list.length > 0
} else {
Toast(res.data.msg)
}
})
}
}
}



