
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'buySucces',
directives: {
TransferDom
},
components: {
Confirm
},
data() {
return {
show: false,
ordersDetail: ''
}
},
mounted() {
this.getOrdersDetail ()
},
methods: {
onConfirm() {

},
getOrdersDetail () {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/order_list/detail`,
{
order_id: this.$route.query.orderId
}).then(res => {
if (res.data.code === 200) {
this.ordersDetail = res.data.data
} else {
console.log(res.data.msg)
}
})
}
}
}



