
import { Toast } from 'mint-ui'
import Icon from 'vux/src/components/icon/index.vue'

export default {
components: { Icon },
data () {
return {
type: 'password',
par: {
username: localStorage.getItem('mobile') === 'undefined' ? '' : localStorage.getItem('mobile'),
password: '',
device_type: 'web'
},
tips: {
user: '请输入正确的登录帐号',
pwd: '密码错误'
}
}
},
mounted () {
localStorage.removeItem('token')
},
methods: {

changeType () {
if (this.type === 'password') {
this.type = 'text'
} else {
this.type = 'password'
}
},
/**
* 登录
* @returns {boolean}
*/
getLoad () {
let status
if (!this.par.username) {
Toast('登录帐号不能为空！')
status = true
}
if (!this.par.password) {
Toast('登录密码不能为空！')
status = true
}
if (this.par.username && !/^(1[3-9])\d{9}$/.test(this.par.username)) {
Toast('登录帐号格式不正确！')
status = true
}
if (status) {
return false
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/public/login`, this.par).then(res => {
Toast(res.data.msg)
if (res.data.code === 200) {
localStorage.setItem('mobile', res.data.data.mobile)
localStorage.setItem('uid', res.data.data.id)
localStorage.setItem('xx-token', res.data.data.token)
localStorage.setItem('user_nickname', res.data.data.user_nickname)

this.$root.mobile = res.data.data.mobile
this.$root.token = res.data.data.token
this.$root.avatar = res.data.data.avatar
this.$root.sex = res.data.data.sex
this.$root.user_nickname = res.data.data.user_nickname
this.$root.uid = res.data.data.id
if (this.$route.query.type && this.$route.query.type === 2) {
this.$router.go(-2)
} else {
this.$router.push('/index')
}
}
}).catch(err => {
console.log(err)
})
}
}
}



