import {Toast} from 'mint-ui'
export default {
    name: 'ordersItem',
    data() {
        return {
            ordersDetail: ''
        }
    },
    mounted() {
        this.getOrdersDetail()
    },
    filters: {
        formatStatus (val) {
            let str = ''
            if (!val) return str = ''
            if (val === 'done') {
                str = '已完成'
            } else if (val === 'close') {
                str = '已取消'
            } else if (val === 'payed') {
                str = '交易成功'
            } else if (val === 'paying') {
                str = '待付款'
            }
            return str
        }
    },
    methods: {
        getOrdersDetail() {
            this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/order_list/detail`,
                {
                    // uid: this.$root.uid || localStorage.getItem('uid'),
                    token: this.$root.token || localStorage.getItem('token'),
                    order_id: this.$route.query.orderId
                }).then(res => {
                if (res.data.code === 200) {
                    this.ordersDetail = res.data.data
                } else {
                    console.log(res.data.msg)
                }
            })
        }
    }
}



