
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import { Toast } from 'mint-ui'
import userAvatar from '../../assets/img/titleUser.png'
export default {
directives: {
TransferDom
},
components: {
Popup
},
data () {
return {
isAction: false,
uploadToken: '',
defaultImg: 'this.src="' + userAvatar + '"',
userInfo: '',
showUrl: 'http://qiniu.xinleapp.com/'
}
},

mounted() {
this.getUser()
},
methods: {
getImage() {
console.log(window.plus)
},
/**
* 获取用户信息
*/
getUser() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/userInfo`,
{
token: this.$root.token || localStorage.getItem('token'),
}).then(res => {
if(res.data.code === 200) {
this.userInfo = res.data.data
}
})
},
/**
* 修改头像
* @param e
*/
update(e){
this.isAction = false
let file = e.target.files[0],
param = new FormData();
param.append('chunk','0');
param.append('chunks','1');
param.append('file',file,file.name)
let config = {
headers:{'Content-Type':'multipart/form-data'}
};

this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/getUploadToken`).then(res => {
this.uploadToken = res.data.data.token
param.append('token',this.uploadToken);
this.uploading(param,config);
return;
})
},
/**
* 上传到七牛
*/
uploading(param,config){
this.$http.post('http://upload.qiniup.com/',param,config).then(res => {
this.userInfo.avatar = this.showUrl + res.data.key
this.changeUserInfo()
})
},
/**
* 修改个人信息
*/
changeUserInfo() {
let par = {
id: this.userInfo.ref_uid,
sex: this.userInfo.sex,
avatar: this.userInfo.avatar
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/modify`,par).then(res => {
Toast(res.data.msg)
})
},
}
}



