import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      par: {
        total: this.$route.query.total || 100,
        payType: 'wechat',
        fullVal: 1,
        balance: 0,
        balanceVal: ''
      },
      price: '',
      payWay: {
        wxPay: true,
        aliPay: true
      },
      voucher: localStorage.getItem('voucherChecked') ? JSON.parse(localStorage.getItem('voucherChecked')) : '',
      address: localStorage.getItem('position'),
      shop: JSON.parse(localStorage.getItem('shop')),
      shopDetail: '',
      token: this.$route.query.token,
      uid: this.$route.query.user_id,
      isShow: false,
      payJson: ''
    }
  },
  computed: {
    /**
     * 计算实付金额
     * @returns {number}
     */
    payMoney() {
      let total = parseFloat(this.par.total),
          arry = [], // 满减
          max = '',
          mix = '',
          arr = [], //代金券
          price = '',
          full = '',
          min = 0,
          balance = this.par.balanceVal,
         dis = ''
      if (this.par.fullVal === 1) { // 已选择满减时
        if (this.coupon.indexOf('减') > -1) {
          arry = this.coupon.split('减')
          max = parseInt(arry[0].replace(/[^0-9]/ig,""))
          mix = total >= max ? parseInt(arry[1]) : 0
        } else if (this.coupon.indexOf('折') > -1) {  // 优惠方式为折扣时
          mix = total >= max ? total * (1 - parseFloat(this.coupon)/10) : 0
        }
      }

      if (this.voucher.items) {
        this.voucher.items.map(item => {
            min += parseInt(item.deduction)
        })
        // arr = this.voucher.split('代') // 已选择代金券时
        // price = parseInt(arr[0].replace(/[^0-9]/ig,""))
        // full = parseInt(arr[1])
        // min = full - price
      }
      if (!total) return 0
      total = (total - min - (min === 0 ? mix : 0) - (this.par.balance === 2 ? balance : 0))
      return total >=0 ? total.toFixed(2) : 0
    }

  },
  mounted () {
      this.changePrice()
      this.getBrowser()
  },
  methods: {
    /**
     * 计算实付金额
     */
    changePrice() {
      let par = {
        shop_code: this.$route.query.shop_code || '',
        shop_id: this.$route.query.shopId || '',
        order_type: 1,
        total_amount: this.par.total,
        coupon_id: this.voucher ? JSON.stringify(this.voucher.itemId): '',
        useBalance: this.par.balance || 0
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/shop_order/changePrice`,par).then(res => {
          if (res.data.code === 200) {
              this.price = res.data.data
          }
        })
    },
    /**
     * 确认付款
     */
    confirmPay() {
      let par = {
        shop_code: this.$route.query.shop_code || '',
        shop_id: this.$route.query.shopId || '',
        order_type: 1,
        total_amount: this.par.total,
        payment_type: this.$root.userAgent === 'web' ? this.par.payType : ''
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/pay/pay/submit`,par).then(res => {
        if (res.data.code === 200) {
          this.payJson = res.data.data
          if(this.par.payType === 'wechat') {
            this.wxPay()
          } else {
            this.aliPay()
          }
        }
      })
    },
    /**
     * 微信付款
     */
    wxPay() {
      let u = navigator.userAgent
      let href = window.location.href
      let host = window.location.host
      if (u.indexOf('MicroMessenger') > -1) {
        WeixinJSBridge.invoke('getBrandWCPayRequest', this.payJson.payJson, function (res) {
          if (res.err_msg == 'get_brand_wcpay_request:ok') {
            window.location.href = `${host}/#/receiveRed?orderId=${this.payJson.order_id}`
          } else {
            window.location.href = href
          }
        })
      } else {
        // this.$http.post(`https://api.mch.weixin.qq.com/pay/unifiedorder`,data.payJson).then(res => {
        //
        // })
      }
    },
    /**
     * 支付宝付款
     */
    aliPay() {
      let u = navigator.userAgent
      let href = window.location.href
      let host = window.location.host
      ap.tradePay({tradeNO: this.payJson.payJson},function(res){
        if(res.resultCode != 9000) {
          Toast('支付失败')
          window.location.href = href
        } else  {
          Toast('支付成功')
          window.location.href = `${host}/#/receiveRed?orderId=${this.payJson.order_id}`
        }
      })
    },
    //获取是否为微信浏览器
    getBrowser() {
      let u = navigator.userAgent
      if (u.indexOf('MicroMessenger') > -1) {
        this.par.payType = 'wechat'
        this.payWay.wxPay = true
        this.payWay.aliPay = false
      } else if(u.indexOf('Alipay') > -1){
        this.par.payType = 'alipay'
        this.payWay.wxPay = false
        this.payWay.aliPay = true
      }
      this.isShow = this.voucher && this.voucher.itemId.length > 0
      this.getBalance()
    },
    getBalance(){
      let par = {
        token: this.$root.token || localStorage.getItem('xx-token')
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/wallet/balance`, par).then(res => {
        if (res.data.code === 200) {
          this.par.balanceVal = res.data.data;
        } else {
          Toast(res.data.msg)
        }
      })
    }
  }
}



