import {Toast} from 'mint-ui'
export default {
  name: 'voucherList',
  data () {
    return {
      shopId: this.$route.query.shopId,
      dateTime: new Date().getTime(),
      pasted: true,
      couponList: [],
      page: 0,
      voucherChecked: {
        itemId: [],
        items: []
      },
      items: [],
      total: this.$route.query.total
    }
  },
  filters: {
    /**
     * 代金券是否过期
     * @param val
     * @param timestamp
     * @returns {string}
     */
    formatType(val) {
      let str = ''
      if(!val && val !== 0) return str = ''
      if (val === 1) {
          str = '已使用'
      } else if (val === 0) {
          str = '未使用'
      } else if (val === 2) {
          str = '已过期'
      }
      return str
    }
  },
  computed: {

  },
  mounted() {
      this.getVoucherList()
  },
  methods: {
    /**
     * 反选代金券
     */
    getCheckList() {
      let data = JSON.parse(localStorage.getItem('voucherChecked')) || ''
      if(data.items.length > 0) {
        data.items.map(tmp => {
          this.couponList.map(item => {
            if(tmp.id === item.id) {
              this.$set(item, 'checked', true)
              this.voucherChecked.itemId.push(item.id)
              this.items.push(item)
            }
          })
        })
        this.voucherChecked.items = Object.assign([],this.items)
      }
    },
    /**
     * 选择代金券
     * @param item
     * @param index
     */
      checkedVoucher(item,index) {
          let total = parseFloat(this.total)
          item.checked = !item.checked
          if(item.checked) {
            this.items.push(item)
            this.voucherChecked.itemId.push(item.id)
          } else {
            for(var i in this.voucherChecked.itemId) {
              if(this.voucherChecked.itemId[i] === item.id) {
                this.voucherChecked.itemId.splice(i, 1)
                this.items.splice(i,1)
              }
            }
          }
          this.voucherChecked.items = Object.assign([],this.items)
      },
    /**
     * 选择代金券后确定跳转
     * @returns {boolean}
     */
      confirm() {
        if(!this.voucherChecked.itemId) {
          Toast("请选择代金券")
          return  false
        }
        localStorage.setItem('voucherChecked', JSON.stringify(this.voucherChecked))
        // this.$router.push({path: '/pay',query: {total: this.total,shopId: this.$route.query.shopId}})
      this.$router.back()
      },
    /**
     * 代金券列表
     */
      getVoucherList() {
        this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/shop_order/getCouponList`,
          { shop_id: this.$route.query.shopId}).then(res => {
          if (res.data.code === 200) {
              this.couponList = res.data.data
            let data = JSON.parse(localStorage.getItem('voucherChecked')) || ''
            console.log(data)
              for(let i in this.couponList) {
                  this.$set(this.couponList[i], 'checked', false)
              }
          this.getCheckList()
          }
        })
      }
  }
}



