
import { Toast } from 'mint-ui'
import Icon from 'vux/src/components/icon/index.vue'

export default {
components: { Icon },
data () {
return {
type: 'password',
par: {
username: localStorage.getItem('mobile') === 'undefined' ? '' : localStorage.getItem('mobile'),
code: '',
password: '',
passwords: ''
},
tips: {
user: '请输入正确的登录帐号',
pwd: '密码错误'
}
}
},
mounted () {

},
methods: {

changeType () {
if (this.type === 'password') {
this.type = 'text'
} else {
this.type = 'password'
}
},

getCode () {
let status
if (!this.par.username) {
Toast('手机号不能为空！')
status = true
}
if (status) {
return false
}
this.$http.post(`${this.$root.baseUrl}/api/base/public/getCode`, {mobile: this.par.username}).then(res => {
Toast(res.data.msg)
if (res.code === 200) {
this.$router.push({name: '/login'})
}
}).catch(err => {
console.log(err)
})
},
/**
* 修改密码
* @returns {boolean}
*/
getLoad () {
let status
if (!this.par.username) {
Toast('手机号不能为空！')
status = true
}
if (!this.par.password) {
Toast('新密码不能为空！')
status = true
}
if (!this.par.passwords) {
Toast('确认密码不能为空！')
status = true
}
console.log(this.par.passwords, this.par.password)
if (this.par.passwords !== this.par.password) {
Toast('两次密码输入不一致！')
status = true
}
if (this.par.username && !/^(1[3-9])\d{9}$/.test(this.par.username)) {
Toast('手机号格式不正确！')
status = true
}
if (status) {
return false
}
let par = {
username: this.par.username,
password: this.par.password,
code: this.par.code
}
this.$http.post(`${this.$root.baseUrl}/api/user/public/passwordReset`, this.par).then(res => {
Toast(res.data.msg)
if (res.data.code === 200) {

}
}).catch(err => {
console.log(err)
})
}
}
}



