import { Toast } from 'mint-ui'
export default {
  name: 'ordersPay',
  data() {
    return {
      checkType: '1',
      ordersDetail: ''
    }
  },
  mounted() {
    this.getOrdersDetail()
  },
  methods: {
    getOrdersDetail() {
      let par = {
        order_id: this.$route.query.orderId,
        payment_type: this.$route.query.payType
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/shop_order/toPay`,par).then(res => {
          if(res.data.code === 200) {
            this.ordersDetail = res.data.data
          }
      })
    }
  }
}



