
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
name: 'picView',
components: {
Swiper,SwiperItem
},
props: ['data'],
data() {
return {
swiperItemIndex:  0,
showDots: false,
parentShow: false
}
},
methods: {
sendMsgToParent() {
this.$emit('listenToPic', this.parentShow)
this.swiperItemIndex =  0
}
}
}



