
import Icon from 'vux/src/components/icon/index.vue'

export default {
name: 'search',
components: { Icon },
data() {
return {
boxShow: true,
nothing: false,
searchVal: '',
status: false,
historyShow:false,
loading: false,
shopList: [],
searchHis: [],
hotData: [],
pages: {
page: 0
}
}
},
mounted() {
this.getSearchKeyWord()
let local = localStorage.getItem("searchHis");
this.searchHis = [];
if (local != null) {
this.searchHis = JSON.parse(local);
this.historyShow = true;
}
},
methods: {
/**
* 清空搜索框
*/
clearVal () {
this.searchVal = ''
this.boxShow = true
},
/**
* 删除历史搜索
*/
del () {
localStorage.setItem("searchHis", null);
this.searchHis = [];
this.historyShow = false;
},
/**
* 获取热门搜索关键字
*/
getSearchKeyWord () {
let par = {}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/home/site/searchKeyword`, par).then(res => {
if (res.data.code === 200) {
this.hotData = res.data.data;
} else {
Toast(res.data.msg)
}
})

},
/**
* 搜索框输入
*/
search () {
if (this.searchHis == null) this.searchHis = [];
if (this.searchHis.indexOf(this.searchVal) == -1) {
this.searchHis.push(this.searchVal)
localStorage.setItem('searchHis', JSON.stringify(this.searchHis))
}
if (this.searchHis.length > 0) {
this.historyShow = true;
}
this.$router.push({path:'/searchList',query: {keyword: this.searchVal}})
},

/**
* 热门搜索
*/
searchHot (val) {
this.searchVal = val
if (this.searchHis.length > 0) {
this.historyShow = true;
}
if (this.searchHis.indexOf(this.searchVal) == -1) {
this.searchHis.push(val)
localStorage.setItem('searchHis', JSON.stringify(this.searchHis))
}
this.$router.push({path:'/searchList',query: {keyword: this.searchVal}})
}
}
}



