import axios from 'axios'
import qs from 'qs'
import isPlainObject from 'lodash/isPlainObject'
import Vue from 'vue'
import router from '../../router'

const http = axios.create({
  // crossDomain: true,
  timeout: 1000 * 30,
  withCredentials: false
})

 http.interceptors.request.use(config => {
   // Vue.$vux.loading.show({
   //   text: '加载中...'
   // })
   if(config.data) {
     if(config.data.page && config.data.page > 0) {
       Vue.$vux.loading.hide()
     } else {
       Vue.$vux.loading.show({
         text: '加载中...'
       })
     }
   }

   if(localStorage.getItem('xx-token')) {
     config.headers = {
       'xx-Token': localStorage.getItem('xx-token'),
       'xx-Device-Type': "web"
     }
   }
   return config
}, error => {
  Vue.$vux.loading.hide()
  return Promise.reject(error)
})

  // if (config.method === 'post') {
  //     config.data = qs.stringify(config.data)
  //     if (/^application\/x-www-form-urlencoded/.test(config.headers['content-type'])) {
  //           config.data = qs.stringify(config.data)
  //     }
  //   }
  // console.log(config)
  // 默认参数
  // var defaults = {
  //   '_t': new Date().getTime()
  // }
// })

/**
 * 响应拦截
 */
http.interceptors.response.use(response => {
    Vue.$vux.loading.hide()
    if(response.data.code === 10001) {
        localStorage.removeItem('xx-token')
        localStorage.removeItem('uid')
      let u = navigator.userAgent
      if (u.indexOf('MicroMessenger') > -1 || u.indexOf('Alipay') > -1) {
        if (!window.location.href.split('?')[1].indexOf('token') > -1) {
          // window.location.href = `${window.location.href}${window.location.href.indexOf('?') > -1 ? '&' : '?'}t=${Math.random()}`

          if (u.indexOf('MicroMessenger') > -1) {
             window.location.href = `http://test.xinleapp.com/portal/wechat/oauth?target_url=${encodeURIComponent(window.location.href)}`
          } else if (u.indexOf('Alipay') > -1) {
             window.location.href = `http://test.xinleapp.com/portal/ali/oauth?target_url=${encodeURIComponent(window.location.href)}`
          }
        } else {
          alert(window.location.href.split('?')[1].indexOf('token') > -1)
        }
        // else {
        //   localStorage.setItem('xx-token', this.$route.query.token)
        //   localStorage.setItem('uid', this.$route.query.user_id)
        // }
      } else {
        router.push('/noLogin')
      }
    }
    return response
}, error => {
    Vue.$vux.loading.hide()
    return Promise.reject(error)
})

export default http



