export default {
    name: "redPacket",
  data () {
      return {
        bonusList: [],
        isNothing: false
      }
  },
  filters: {
      formatStatus([val,timestamp]) {
        let str,
          newTimes = new Date().getTime(),
          endTime = timestamp * 1000
        if (!val && val !== 0) return str = ''
        if (val === 1) {
          str = '已使用'
        } else if(newTimes > endTime) {
          str = '已过期'
        }
        return str
      }
  },
  mounted() {
    this.getBonusList()
  },
  methods: {
    getBonusList() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/bonusList`,
        {type: 'sx'}).then(res => {
        if (res.data.code === 200) {
          this.bonusList = res.data.data
          this.isNothing = !this.bonusList.length > 0
        }
      })
    }
  }
}



