

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import Mint from 'mint-ui'
import http from './utils/js/http'
import '@/icons'
import 'mint-ui/lib/style.css'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import { Toast } from 'mint-ui'

Vue.use(LoadingPlugin)


Vue.prototype.$http = http

Vue.config.productionTip = false

Vue.use(Mint)

Vue.prototype.show = localStorage.getItem("visitor") || false;

router.beforeEach((to, from, next) => {
Vue.prototype.$title = to.query.name || to.meta.title
if(to.path == '/shopDetail' || to.path == '/buySuccess') {
localStorage.removeItem('voucherChecked')
}
let token = localStorage.getItem('xx-token')
if(to.query.token && !token) {
localStorage.setItem('xx-token', to.query.token)
localStorage.setItem('uid', to.query.user_id)
}
if(to.path == '/pay') {
document.title = to.query.shopName
} else {
document.title = '纷米'
}
next()
})

/* eslint-disable no-new */
let Root = new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>',
data: {
userAgent: '',
baseUrl: '',
lng: 116.331398,
lat: 39.897445,
address: '',
point: '',
token: '',
mobile: '',
sessid: '',
user_nickname: '',
avatar: '',
sex: '',
uid: '',
cate_id: '',
browser: ''
},
beforeMount() {
this.getDevice()
this.getCity()
},
mounted () {},
filters: {
/**
* 订单类型
* @param val
* @returns {string}
*/
formatType (val) {
let str = ''
if (!val) return str = ''
if (val === 1) {
str = '买单'
} else if (val === 2) {
str = '购买代金券'
} else if (val === 3) {
str = '购物'
}
return str
},
/**
* 订单状态
* @param val
* @returns {string}
*/
formatStatus (val) {
let str = ''
if (!val) return str = ''
if (val === 'close') {
str = '已取消'
} else if (val === 'paying') {
str = '待付款'
} else if (val === 'payed') {
str = '已付款'
} else if (val === 'done') {
str = '已完成'
}
return str
}
},
methods: {
/**
* 获取定位
*/
getCity () {
var point = new BMap.Point(this.lng, this.lat)
var geolocation = new BMap.Geolocation()
geolocation.getCurrentPosition(r => {
this.address = r.address
this.lng = r.point.lng
this.lat = r.point.lat
}, {
enableHighAccuracy: true
})
},
/**
* 获取设备信息,微信授权
* @returns {string}
*/
getDevice () {
let u = navigator.userAgent,
token = localStorage.getItem('xx-token')

if (!token) {
if (!this.$route.query.token) {
if (u.indexOf('MicroMessenger') > -1) {
window.location.href = `http://test.xinleapp.com/portal/wechat/oauth?target_url=${encodeURIComponent(window.location.href)}`
} else if (u.indexOf('Alipay') > -1) {
window.location.href = `http://test.xinleapp.com/portal/ali/oauth?target_url=${encodeURIComponent(window.location.href)}`
}
} else {
alert(this.$route.query.token)
localStorage.setItem('xx-token', this.$route.query.token)
localStorage.setItem('uid', this.$route.query.user_id)
}
}
if(u.indexOf('MicroMessenger') > -1) {
this.userAgent = 'wechat'
} else if (u.indexOf('Alipay') > -1) {
this.userAgent = 'alipay'
} else {
this.userAgent = 'web'
}
return this.userAgent
},
/**
* 时间格式化
* @param date
* @param fmat
* @returns {*}
*/
formatDate (timestamp,type) {
var date = new Date(timestamp * 1000);
var Y = date.getFullYear() + '-';
var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
var D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate() + ' ';
var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours() + ':';
var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();

return type === 'YY-MM-DD' ? Y+M+D : Y+M+D+' '+h+m;
},
/**
* 日期格式
* @param d
* @param fmt
* @returns {*}
*/
dateFormat(d, fmt){ //author: meizz
var date = new Date(d);
var o = {
"m+": date.getMonth() + 1, //月份
"d+": date.getDate(), //日
"h+": date.getHours(), //小时
"i+": date.getMinutes(), //分
"s+": date.getSeconds(), //秒
"q+": Math.floor((date.getMonth() + 3) / 3), //季度
"S": date.getMilliseconds() //毫秒
};
if (/(y+)/i.test(fmt)){
fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
}
for (var k in o){
if (new RegExp("(" + k + ")", 'i').test(fmt)){
fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
}
}
return fmt;
}
}
})



