
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: 'myWallet',
components: {
Confirm
},
directives: {
TransferDom
},
data() {
return {
total: 0,
show:false,
}
},
mounted() {
this.getBalance();
},
methods: {
showConfirm(){
this.show = true;
},
onConfirm () {
this.show = false;
this.$router.push('/download')
},
getBalance(){
let par = {}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/wallet/balance`, par).then(res => {
if (res.data.code === 200) {
this.total = res.data.data;
} else {
Toast(res.data.msg)
}
})
}
}

}



