import { Toast } from 'mint-ui'
export default {
  name: 'ordersCommit',
  data() {
    return {
      ordersNum: 1,
      itemDetail: JSON.parse(localStorage.getItem('itemDetail')),
      phone: localStorage.getItem('mobile'),
      payType: 'wechat',
      payWay: {
        wxPay: true,
        aliPay: true
      },
      payJson: '',
      bonus_money: 0,
      pay_amount: JSON.parse(localStorage.getItem('itemDetail')).discount_price || JSON.parse(localStorage.getItem('itemDetail')).price,
    }
  },
  mounted() {
    this.getBrowser()
    this.changePrice()
  },
  methods: {
    /**
     * 提交订单并支付
     */
    ordersCommit() {
      let product = [{
        product_id: this.$route.query.id,
        num: this.ordersNum
      }]
      let par = {
        shop_code: this.$route.query.shop_code || '',
        shop_id: this.$route.query.shopId || '',
        order_type: this.$route.query.type,
        buy_coupon: this.$route.query.type == 3 ? this.$route.query.id : '',
        num: this.$route.query.type == 3 ? this.ordersNum : '',
        product:  this.$route.query.type == 3 ? JSON.stringify(product) : '',
        payment_type: this.$root.userAgent == 'web' ? this.payType : '',
        redPacketId: '',
        coupon_id: '',
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/pay/pay/submit`,par).then(res => {
        if (res.data.code === 200) {
          this.payJson = res.data.data
          if(this.payType === 'wechat') {
            this.wxPay()
          } else {
            this.aliPay()
          }
        }
      })
    },
    /**
     * 微信付款
     */
    wxPay() {
      let u = navigator.userAgent
      let href = window.location.href
      let host = window.location.host
      if (u.indexOf('MicroMessenger') > -1) {
        WeixinJSBridge.invoke('getBrandWCPayRequest', this.payJson.payJson, function (res) {
          if (res.err_msg == 'get_brand_wcpay_request:ok') {
            window.location.href = `${host}/#/receiveRed?orderId=${this.payJson.order_id}`
          } else {
            window.location.href = href
          }
        })
      } else {
        // this.$http.post(`https://api.mch.weixin.qq.com/pay/unifiedorder`,data.payJson).then(res => {
        //
        // })
      }
    },
    /**
     * 支付宝付款
     */
    aliPay() {
      let u = navigator.userAgent
      let href = window.location.href
      let host = window.location.host
      ap.tradePay({tradeNO: this.payJson.payJson}, function(res){
        if(res.resultCode != 9000) {
          Toast('支付失败')
          window.location.href = href
        } else  {
          Toast('支付成功')
          window.location.href = `${host}/#/receiveRed?orderId=${this.payJson.order_id}`
        }
      })
    },
    //获取是否为微信浏览器
    getBrowser() {
      let u = navigator.userAgent
      if (u.indexOf('MicroMessenger') > -1) {
        this.payType = 'wechat'
        this.payWay.wxPay = true
        this.payWay.aliPay = false
      } else if(u.indexOf('Alipay') > -1){
        this.payType = 'alipay'
        this.payWay.wxPay = false
        this.payWay.aliPay = true
      }
    },
    /**
     * 计算实付金额
     */
    changePrice() {
      let product = [{
        product_id: this.$route.query.id,
        num: this.ordersNum
      }]
      let par = {
        shop_code: this.$route.query.shop_code || '',
        shop_id: this.$route.query.shopId || '',
        order_type: this.$route.query.type,
        buy_coupon: this.$route.query.type == 2 ? this.$route.query.id : '',
        num: this.$route.query.type == 2 ? this.ordersNum : '',
        coupon_id: '',
        product: this.$route.query.type == 3 ? JSON.stringify(product) : '',
        redPacketId: '',
        useBalance: 0
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/shop_order/changePrice`,par).then(res => {
        if (res.data.code === 200) {
          this.pay_amount = res.data.data.pay_amount
          this.bonus_money = res.data.data.bonus_money
        }
      })
    },
    /**
     * 购买数量加减
     * @param type
     */
    changeNum(type) {
      if(type === 'add') {
        this.ordersNum += 1
        this.changePrice()
      } else if(type === 'min' && this.ordersNum > 1) {
        this.ordersNum -= 1
        this.changePrice()
      }
    }
  }
}



