
import {Toast, Loadmore} from 'mint-ui'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import userAvatar from '../../assets/img/titleUser.png'
export default {
name: 'collect',
components: {
InlineLoading, Loadmore
},
data() {
return {
defaultImg: 'this.src="' + userAvatar + '"',
status: true,
loading: false,
isShow: false,
isTab: true,
isClass: false,
teamList:[],
tag: [],
pages: {
page: 0
}
}
},
mounted() {
this.getTeamList('down');
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.pages.page += 1
this.getTeamList('down')
},2500)
} else {
this.loading = false
}
},
getTeamList(type){
this.isClass = false
let par = {
type: 'shop',
page: this.pages.page,
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/hierarchies`, par).then(res => {
if (res.data.code === 200) {
if (!this.teamList || (!type && type !== 'down')) {
this.teamList = res.data.data
setTimeout(() => {
this.isClass = true
},2000)
} else {
for (let i in res.data.data) {
this.teamList.push(res.data.data[i])
}
}
this.status = res.data.data.length < 15 ? false : true
this.loading = false
this.isShow = this.teamList.length === 0
} else {
Toast(res.data.msg)
}
})
}

}
}



