export default {
    name: 'allMenu',
    data() {
        return {
            itemIndex: 0,
            menu: [],
            cateList: []
        }
    },
    mounted() {
        this.getCateList();
    },
    methods: {
      scroll(selector,index) {
        this.itemIndex = index
        this.$nextTick(() => {
          document.body.scrollTop = this.$el.querySelector(selector).offsetTop
        })
      },
        getCateList() {
            let par = {
                parent_id: '',
            }
            this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/category`, par).then(res => {
                if (res.data.code === 200) {
                    this.cateList = res.data.data;
                } else {
                    Toast(res.data.msg)
                }
            })
        }
    }
}



