
import userAvatar from '../../assets/img/titleUser.png'
import Qrcode from 'vux/src/components/qrcode/index.vue'

export default {
name: 'share',
components: { Qrcode },
data() {
return {
url: '',
userInfo: '',
defaultImg:'this.src="'+userAvatar+'"'
}
},
mounted() {
this.getUrl()
this.getUser()
},
methods: {
getUser() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/userInfo`).then(res => {
if(res.data.code === 200) {
this.userInfo = res.data.data
}
})
},
getUrl() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/getQrcode`,{type: 'user'}).then(res => {
if(res.data.code === 200) {
this.url = res.data.data.url
}
})
}
}
}



