
import {Toast} from 'mint-ui'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
name: 'searchList',
components: {
InlineLoading, Icon, Scroller,LoadMore, Divider
},
data() {
return {
boxShow: true,
nothing: false,
searchVal: this.$route.query.keyword,
status: false,
historyShow:false,
loading: false,
shopList: [],
searchHis: [],
hotData: [],
pages: {
page: 0
}
}
},
mounted() {
this.getSearchShop()
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
})
},
methods: {
clearVal() {
this.searchVal = ''
this.boxShow = true
},
search() {
if(this.searchHis == null) this.searchHis=[];
if (this.searchHis.indexOf(this.searchVal) == -1) {
this.searchHis.push(this.searchVal)
localStorage.setItem('searchHis', JSON.stringify(this.searchHis))
}
if(this.searchHis.length > 0){
this.historyShow = true;
}
this.getSearchShop()
},
/**
* 加载更多
*/
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true
setTimeout(() => {
this.pages.page += 1
this.getSearchShop()
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
this.onFetching = false
}, 2500)
}
},
/**
* 获取热门商家
*/
getSearchShop(val) {
this.boxShow = false
let params = {
city_code: this.$root.address.city_code,
lng: this.$root.lng,
lat: this.$root.lat,
page: this.pages.page,
keyword: this.searchVal
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/home/site/search`, params).then(res => {
if (res.data.code === 200) {
if (!this.shopList) {
this.shopList = res.data.data
} else {
for (let i in res.data.data) {
this.shopList.push(res.data.data[i])
}
}
this.loading = false
this.status = res.data.data.length >= 15 ? false : true
this.onFetching = res.data.data.length >=15 ? false : true
this.nothing = !this.shopList.length > 0
} else {
Toast(res.data.msg)
}
})
}
}
}



