
import { Toast, Loadmore } from 'mint-ui'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import defaultImg from '../../assets/img/defaultShop.png'
export default {
name: 'collect',
components: {
InlineLoading, Loadmore
},
data() {
return {
defaultImg: 'this.src="' + defaultImg + '"',
status: true,
loading: false,
isShow: false,
status2: true,
loading2: false,
isShow2: false,
isTab: true,
isClass: false,
isClass2: false,
shopList:[],
productList:[],
tag: [],
pages: {
page: 0,
page2: 0
},
shopId:123456
}
},
mounted() {
this.getFavShopList('down');
this.getFavProductList('down');
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.isTab && this.status) {
this.loading = true
this.pages.page += 1
setTimeout(() => {
this.getFavShopList('down');
},2500)
} else if (!this.isTab && this.status2) {
this.loading2 = true
this.pages.page2 += 1
setTimeout(() => {
this.getFavProductList('down');
},2500)
} else {
this.loading = false
this.loading2 = false
}
},
getFavShopList(type){
this.isClass = false
let par = {
type: 'shop',
page: this.pages.page,
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/collectionList`, par).then(res => {
if (res.data.code === 200) {
if (!this.ordersHand || (!type && type !== 'down')) {
this.shopList = res.data.data
setTimeout(() => {
this.isClass = true
},2000)
} else {
for (let i in res.data.data) {
this.shopList.push(res.data.data[i])
}
}
this.status = res.data.data.length < 15 ? false : true
this.loading = false
this.isShow = !this.shopList.length
} else {
Toast(res.data.msg)
}
})
},
getFavProductList(type){
this.isClass2 = false
let par = {
type: 'product',
page: this.pages.page2,
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/collectionList`, par).then(res => {
if (res.data.code === 200) {
if (!this.productList || (!type && type !== 'down')) {
this.productList = res.data.data
setTimeout(() => {
this.isClass2 = true
},2000)
} else {
for (let i in res.data.data) {
this.productList.push(res.data.data[i])
}
}
this.status2 = res.data.data.length < 15 ? false : true
this.loading2 = false
this.isShow2 = !this.productList.length
} else {
Toast(res.data.msg)
}
})
}
}
}



