
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
components: {
Tabbar,
TabbarItem
},
data () {
return {
index: 0
}
},
watch: {
'$route': 'watchRoute' // 如果路由有变化，会执行该方法
},
methods: {
changeIndex (newIndex, oldIndex) {
this.index = newIndex
localStorage.setItem('barIndex',this.index)
},
watchRoute () {
if (this.$route.path === '/index') {
this.index = 0
} else if (this.$route.path === '/ordersAll') {
this.index = 1
} else if (this.$route.path === '/users') {
this.index = 2
}
}
}
}



