import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/home'
import Login from '@/login'
import Register from '@/register'
import SetPassword from '@/SetPassword'
import Index from '../page/index'
import Pay from '../page/orders/pay'
import HistoryEarn from '../page/my/historyEarn'
import TodayEarn from '../page/my/todayEarn'
import Orders from '../page/orders/ordersPage'
import OrdersAll from '../page/orders/ordersAll'
import OrdersAwait from '../page/orders/ordersAwait'
import OrdersHanding from '../page/orders/ordersHanding'
import OrdersDetail from '../page/orders/ordersDetail'
import OrdersCommit from '../page/orders/ordersCommit'
import OrdersPay from '../page/orders/ordersPay'
import OrdersItem from '../page/orders/ordersItem'
import ProductDetail from '../page/products/productDetail'
import ShopDetail from '../page/products/ShopDetail'
import Users from '../page/my/users'
import Voucher from '../page/my/voucher'
import Coupon from '../page/my/coupon'
import UpdateService from '../page/my/updateService'
import MessagePage from '../page/my/messagePage'
import MessageCenter from '../page/my/messageCenter'
import Share from '../page/my/share'
import Withdraw from '../page/my/withdraw'
import WithdrawCash from '../page/my/withdrawCash'
import Wallet from '../page/my/myWallet'
import Collect from '../page/my/collect'
import Team from '../page/my/team'
import Success from '../page/orders/success'
import FoodPage from '../page/products/foodPage'
import AllMenu from '../page/products/allMenu'
import PhotoAlbum from '../page/products/photoAlbum'
import PicView from '../components/picView'
import Comment from '../page/products/comment'
import CommentDetail from '../page/products/commentDetail'
import ProductList from '../page/products/productList'
import ChangeCity from '../page/changeCity'
import Download from '../page/download'
import Search from '../page/search'
import SearchList from '../page/searchList'
import NoLogin from '../page/noLogin'
import UserInfo from '../page/my/userInfo'
import MyIdentity from '../page/my/myIdentity'
import VoucherList from '../page/products/voucherList'
import VoucherDetail from '../page/products/voucherDetail'
import VoucherMore from '../page/products/voucherMore'
import ChangeName from '../page/my/changeName'
import RedPacketList from '../page/products/redPacketList'
import VoucherChange from '../page/products/VoucherChange'
import RedPacketChange from '../page/products/redPacketChange'
import RedPacketPasted from '../page/products/redPacketPasted'
import BuySuccess from '../page/products/buySuccess'
import ReceiveRed from '../page/receiveRed'
import ProblemFeedback from '../page/orders/problemFeedback'
// import MessagePage from '../page/messagePage'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/login', name: '登录', component: Login, meta: {title: '登录'} },
    { path: '/register', name: '注册', component: Register, meta: {title: '注册'} },
    { path: '/setPassword', name: '忘记密码', component: SetPassword, meta: {title: '修改密码'} },
    { path:'/historyEarn', name:'历史收入', component: HistoryEarn, meta: {title: '历史收入'} },
    { path:'/todayEarn', name:'今日收入', component: TodayEarn, meta: {title: '今日收入'} },
    { path:'/withdraw', name:'提现记录', component: Withdraw, meta: {title: '提现记录'} },
    { path:'/withdrawCash', name:'提现', component: WithdrawCash, meta: {title: '提现'} },
    { path:'/collect', name:'我的收藏', component: Collect, meta: {title: '我的收藏'} },
    { path:'/team', name:'我的团队', component: Team, meta: {title: '我的团队'} },
    { path:'/foodPage', name:'美食', component: FoodPage, meta: {title: '美食'}},
    { path:'/allMenu', name:'全部类目', component: AllMenu, meta: {title: '全部类目'}},
    { path:'/photoAlbum', name:'商家相册', component: PhotoAlbum, meta: {title: '商家相册'}},
    { path:'/picView', name:'图片详情', component: PicView, meta: {title: '图片详情'}},
    { path:'/changeCity', name:'切换城市', component: ChangeCity, meta: {title: '切换城市'}},
    { path:'/comment', name:'用户评论', component: Comment, meta: {title: '用户评论'}},
    { path:'/commentDetail', name:'评价详情', component: CommentDetail, meta: {title: '评价详情'}},
    { path:'/productList', name:'产品列表', component: ProductList, meta: {title: '产品列表'}},
    { path:'/search', name:'搜索', component: Search, meta: {title: '搜索'}},
    { path:'/searchList', name:'搜索列表', component: SearchList, meta: {title: '搜索列表'}},
    { path:'/ordersItem', name:'帐单详情', component: OrdersItem, meta: {title: '帐单详情'}},
    { path:'/noLogin', name:'未登录', component: NoLogin, meta: {title: '未登录'}},
    { path:'/voucherList', name:'代金券', component: VoucherList, meta: {title: '代金券'}},
    { path:'/voucherChange', name:'选择代金券', component: VoucherChange, meta: {title: '选择代金券'}},
    { path:'/voucherDetail', name:'代金券详情', component: VoucherDetail, meta: {title: '代金券详情'}},
    { path:'/voucherMore', name:'更多代金券', component: VoucherMore, meta: {title: '更多代金券'}},
    { path:'/buySuccess', name:'购买成功', component: BuySuccess, meta: {title: '购买成功'}},
    { path:'/receiveRed', name:'领取红包', component: ReceiveRed, meta: {title: '领取红包'}},
    { path:'/problemFeedback', name:'问题反馈', component: ProblemFeedback, meta: {title: '问题反馈'}},

    {
      path: '/',
      name: '首页',
      component: Home,
      children: [
        { path: '/', component: Index, meta: {title: '首页'} },
        { path: '/index', component: Index, meta: {title: '首页'} },
        { path: '/users', component: Users, meta: {title: '我的'} },
        { path: '/ordersDetail', name: '订单详情', component: OrdersDetail,meta: {title: '订单详情'}},
        { path: '/productDetail', name: '产品详情', component: ProductDetail,meta: {title: '产品详情'}},
        {
          path: '/ordersAll',
          name: '我的订单',
          component: Orders,
          children: [
            { path: '/ordersAll', component: OrdersAll, meta: {title: '我的订单'} },
            { path: '/ordersAwait', component: OrdersAwait, meta: {title: '我的订单'} },
            { path: '/ordersHanding', component: OrdersHanding, meta: {title: '我的订单'} },
          ]
        }
      ]
    },
    { path: '/shopDetail', name: '商家详情', component: ShopDetail, meta: {title: '商家详情'}},
    { path: '/share', name: '我的二维码', component: Share, meta: {title: '我的二维码'}},
    { path: '/wallet', name: '我的钱包', component: Wallet, meta: {title: '我的钱包'}},
    { path: '/ordersCommit', name: '提交订单', component: OrdersCommit, meta: {title: '提交订单'}},
    { path: '/ordersPay', name: '支付订单', component: OrdersPay, meta: {title: '支付订单'}},
    { path: '/pay', name: '付款', component: Pay, meta: {title: '付款',keepAlive: true}},
    { path:'/voucher', name:'代金券', component:Voucher, meta: {title: '代金券'} },
    { path:'/coupon', name:'代金券', component:Coupon, meta: {title: '优惠券'} },
    { path:'/redPacketList', name:'红包列表', component:RedPacketList, meta: {title: '红包列表'} },
    { path:'/redPacketChange', name:'选择红包', component:RedPacketChange, meta: {title: '选择红包'} },
    { path:'/redPacketPasted', name:'失效红包', component:RedPacketPasted, meta: {title: '失效红包'} },
    { path:'/updateService', name:'升级服务商', component:UpdateService, meta: {title: '升级服务商'} },
    { path:'/messagePage', name:'系统通知', component:MessagePage, meta: {title: '系统通知'} },
    { path:'/messageCenter', name:'消息中心', component:MessageCenter, meta: {title: '消息中心'} },
    { path:'/success', name:'账单详情', component:Success, meta: {title: '账单详情'} },
    { path:'/download', name:'下载', component:Download, meta: {title: '下载'} },
    { path:'/userInfo', name:'个人信息', component:UserInfo, meta: {title: '个人信息'} },
    { path:'/myIdentity', name:'我的身份', component:MyIdentity, meta: {title: '我的身份'} },
    { path:'/changeName', name:'修改昵称', component:ChangeName, meta: {title: '修改昵称'} }


    // {
    //   path:'/messagePage',
    //   name:'系统通知',
    //   component:MessagePage
    // }
  ]
})



