
import { Toast, Loadmore } from 'mint-ui'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
name: 'ordersHanding',
components: {
InlineLoading, Loadmore
},
data () {
return {
status: true,
loading: false,
ordersHand: [],
isShow: false,
isClass: false,
pages: {
page: 0
}
}
},
filters: {
/**
* 订单类型
* @param val
* @returns {string}
*/
formatType (val) {
let str = ''
if (!val) return str = ''
if (val === 1) {
str = '买单'
} else if (val === 2) {
str = '购买代金券'
} else if (val === 3) {
str = '购物'
}
return str
},
/**
* 订单状态
* @param val
* @returns {string}
*/
formatStatus (val) {
let str = ''
if (!val) return str = ''
if (val === 'close') {
str = '已取消'
} else if (val === 'paying') {
str = '待付款'
} else if (val === 'payed') {
str = '已付款'
} else if (val === 'done') {
str = '已完成'
}
return str
}
},
mounted () {
this.getOrdersAll('down')
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.pages.page += 1
this.getOrdersAll('down')
},2500)
} else {
this.loading = false
}
},
/**
* 进行中订单
*/
getOrdersAll (type) {
this.isClass = false
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/order_list`,
{
token: this.$root.token || localStorage.getItem('token'),
status: 'paying',
page: this.pages.page
}).then(res => {
if (res.data.code === 200) {
if (!this.ordersHand || (!type && type !== 'down')) {
this.ordersHand = res.data.data
setTimeout(() => {
this.isClass = true
},2000)
} else {
for (let i in res.data.data) {
this.ordersHand.push(res.data.data[i])
}
}
this.status = res.data.data.length < 15 ? false : true
this.loading = false
this.isShow = !this.ordersHand.length
} else {
Toast(res.data.msg)
}
})
}
}
}



