
import { Toast } from 'mint-ui'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
Swiper, SwiperItem,InlineLoading
},
data () {
return {
status: false,
loading: false,
showDots: false,
advList: [],
midAdvList: [],
typeList: [],
productList: [],
shopList: [],
pages: {
page: 0
},
isShow: false
}
},
mounted () {
this.getHome()
this.getHomeShop()
this.getCityCode()
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.pages.page += 1
this.getHomeShop()
},2500)
} else {
this.loading = false
}
},
/**
* 获取列表
*/
getHome () {

let par = {
city_code: this.$root.address.city_code,
lng: this.$root.lng,
lat: this.$root.lat,
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/home/site`, par).then(res => {
if (res.data.code === 200) {
this.advList = res.data.data.advList
this.midAdvList = res.data.data.midAdvList
this.typeList = res.data.data.typeList
this.productList = res.data.data.productList
this.isShow = true
} else {
Toast(res.data.msg)
}
})
},
getShopList () {
let params = {
city_code: this.$root.address.city_code,
lng: this.$root.lng,
lat: this.$root.lat,
cate_id: this.$root.cate_id || localStorage.getItem('cateId'),
distance: 1000,
sort: '',
page: this.pages.page
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/home/nearby/ShopList`, params).then(res => {
if (res.data.code === 200) {
this.shopList = res.data.data
} else {
Toast(res.data.msg)
}
})
},
/**
* 获取热门商家
*/
getHomeShop () {
let params = {
city_code: this.$root.address.city_code,
lng: this.$root.lng,
lat: this.$root.lat,
page: this.pages.page
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/home/site/recommedShopList`, params).then(res => {
if (res.data.code === 200) {
if(!this.shopList) {
this.shopList = res.data.data
} else {
for (let i in res.data.data) {
this.shopList.push(res.data.data[i])
}
}
this.loading = false
this.$root.cate_id = this.shopList[0].cate_id
localStorage.setItem('cateId', this.shopList[0].cate_id)
if (res.data.data.length < 15) {
this.status = false
} else if (res.data.data && res.data.data.length === 15){
this.status = true
}
} else {
Toast(res.data.msg)
}
})
},
/**
* 获取城市code
*/
getCityCode() {
if(this.$root.address.city_code === 0) {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/changeCityName`,
{
city_name: this.$root.address.city
}).then(res => {
if (res.data.code === 200) {
this.$root.address.city_code = res.data.data
}
})
}
}
}
}



