import userAvatar from '../../assets/img/titleUser.png'
  export default {
      name: "myIdentity",
    data() {
      return {
        status:true,
        userInfo: '',
        userGroup: '',
        amount: '',
        defaultImg: 'this.src="' + userAvatar + '"'
      }
    },
    mounted() {
      this.getUser()
    },
    methods: {
      getUser() {
        this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/userInfo`).then(res => {
          if(res.data.code === 200) {
            this.userInfo = res.data.data
            this.getUserGroup()
          }
        })
      },
      getUserGroup() {
        this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/getUserGroup`).then(res => {
          if(res.data.code === 200) {
            this.userGroup = res.data.data
          }
        })
      },
      changeimg () {
        if(this.status) {
          this.status = false
        } else {
          this.status = true
        }
      }
    }
  }



