
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
name: 'productList',
components: {
InlineLoading
},
data() {
return {
status: false,
loading: false,
pages: {
page: 0
},
productList: []
}
},
mounted() {
this.getShopList()
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.pages.page += 1
this.getOrdersAll()
},2500)
} else {
this.loading = false
}
},
getShopList() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/product/Lists`,
{
token: this.$root.token || localStorage.getItem('token'),
shop_id: this.$route.query.shopId,
page: this.pages.page
}).then(res => {
if(res.data.code === 200) {
if (!this.productList) {
this.productList = res.data.data
} else {
for (let i in res.data.data) {
this.productList.push(res.data.data[i])
}
if (res.data.data.length < 15) {
this.status = false
} else if (res.data.data && res.data.data.length === 15){
this.status = true
}
}
this.loading = false
}
})
}
}
}



