import userAvatar from '../../assets/img/titleUser.png'
import { Toast } from 'mint-ui'
export default {
  name: 'users',
  data() {
    return {
      userInfo: '',
      amount: '',
      defaultImg: 'this.src="' + userAvatar + '"'
    }
  },
  mounted() {
    this.getUser()
  },
  methods: {
    noClick() {
      Toast('暂时还不能点哦~')
    },
    getUser() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/userInfo`,
        {
          // uid: this.$root.uid || localStorage.getItem('uid')
          token: this.$root.token || localStorage.getItem('xx-token')
      }).then(res => {
        if(res.data.code === 200) {
          this.userInfo = res.data.data
          this.getAccountAmount()
        }
      })
    },
    getAccountAmount() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/getAccountAmount`,
        {
          // uid: this.$root.uid || localStorage.getItem('uid')
          token: this.$root.token || localStorage.getItem('xx-token')
        }).then(res => {
        if(res.data.code === 200) {
          this.amount = res.data.data
        }
      })
    }
  }
}



