export default {
  name: 'voucherDetail',
  data() {
    return {
      couponDetail: '',
      isCollect: true, //未收藏过
      canon: []
    }
  },
  mounted() {
    this.getVoucherDetail()
  },
  methods: {
    /**
     * 收藏代金券
     */
    getCollect() {
      let par = {
        action: 'collection',
        product_id: this.$route.query.couponId,
        type: 1
      }
      if (!this.isCollect) { // 已收藏过
        par.action = 'remove'
        this.isCollect = true
      } else { //未收藏
        par.action = 'collection'
        this.isCollect = false
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/product/productCollection`,par).then(res => {
        Toast(res.data.msg)
        if(res.data.code === 200) {
          this.isCollect = par.action === 'remove'
        }
      })
    },
    toBuyLink() {
      localStorage.setItem('itemDetail',JSON.stringify(this.couponDetail))
      this.$router.push({
        path: '/ordersCommit',
        query: {
          id:this.couponDetail.id,
          shopId: this.couponDetail.shop_id,
          shopName: this.couponDetail.shop_name,
          type: 2
        }})
    },
    getVoucherDetail() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/merchant/coupon/detail`,
        {
          shop_id: this.$route.query.shopId,
          coupon_id: this.$route.query.couponId
        }).then(res => {
        if (res.data.code === 200) {
          this.couponDetail = res.data.data
          this.isCollect = this.couponDetail.whetherCollection === 0
          this.getCanon()
        }
      })
    },
    getCanon() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/merchant/coupon/useTemplate`,
        {
          shop_id: this.$route.query.shopId,
          type_id: this.couponDetail.type
        }).then(res => {
        if (res.data.code === 200) {
          this.canon = res.data.data
        }
      })
    }
  }
}



