
import { Toast } from 'mint-ui'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components:{
Datetime,
Group,
InlineLoading
},
data () {
return {
color: '#f5f5f5',
status: false,
loading: false,
totalData: [],
isShow: false,
pages: {
page: 0
}
}
},
mounted() {
this.getAccount();
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.page += 1
this.getAccount()
},2500)
} else {
this.loading = false
}
},
getAccount () {
let par = {
type: 'today',
page: this.pages.page
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/accountLog`,par).then(res => {
if(res.data.code === 200) {
if (!this.totalData) {
this.totalData = res.data.data
} else {
for (let i in res.data.data) {
this.totalData.push(res.data.data[i])
}
}
if (res.data.data.length < 15) {
this.status = false
} else if (res.data.data && res.data.data.length === 15){
this.status = true
}
this.loading = false
if(!this.totalData.length) {
this.isShow = true
this.color = '#fff'
}
} else {
Toast(res.data.msg)
}
})
}
}
}



