
import { Toast } from 'mint-ui'
import PicView from '../../components/picView'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import userAvatar from '../../assets/img/defaultShop.png'
export default {
directives: {
TransferDom
},
name: 'commentDetail',
components: {
PicView, XDialog
},
data() {
return {
defaultImg: 'this.src="' + userAvatar + '"',
isShow: false,
showDialog: false,
commentList: [],
imgData: [],
pages: {
page: 0
}
}
},
mounted() {
this.getCommentsList()
},
computed: {},
methods: {
getMsgPic(data) {
this.showDialog = data
},
showImgList(list){
this.imgData = list;
this.showDialog=true
},
getCommentsList() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/detail/commentList`,
{

token: this.$root.token || localStorage.getItem('token'),
shop_id: this.$route.query.shopId,
page: this.pages.page
}).then(res => {
if(res.data.code === 200) {
this.commentList = res.data.data.list;
if (this.commentList.length > 0) {
this.isShow = false
} else {
this.isShow = true
}
} else {
Toast(res.data.msg)
}
})
}
}
}



