
import { Toast } from 'mint-ui'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import PicView from '../../components/picView'
export default {
name: 'productDetail',
directives: {
TransferDom
},
components: {
Swiper, SwiperItem,PicView, XDialog
},
data () {
return {
productData: '',
isCollect: true,
showDialog: false,
currentIndex: 0
}
},
mounted () {
this.getProducts()
},
methods: {
/**
* 接收picView组件传值
*/
getMsgPic(data) {
this.showDialog = data
},
/**
* 收藏商品
*/
getCollect() {
let par = {
action: 'collection',
product_id: this.productData.id,
type: 0
}
if (!this.isCollect) { // 已收藏过
par.action = 'remove'
this.isCollect = true
} else { //未收藏
par.action = 'collection'
this.isCollect = false
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/product/productCollection`,par).then(res => {
Toast(res.data.msg)
if(res.data.code === 200) {
this.isCollect = par.action === 'remove'
}
})
},
toBuyLink() {
this.productData.shop_name = this.$route.query.shopName
localStorage.setItem('itemDetail',JSON.stringify(this.productData))
this.$router.push({
path: '/ordersCommit',
query: {
id: this.productData.id,
shopId: this.productData.shop_id,
type: 3
}
})
},
getProducts () {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/product/info`,
{
product_id: this.$route.query.productId,

token: this.$root.token || localStorage.getItem('token')
}).then(res => {
if (res.data.code === 200) {
this.productData = res.data.data
this.isCollect = this.productData.whetherCollection === 0
} else {
Toast(res.data.msg)
}
})
}
}
}



