import { Toast } from 'mint-ui'

export default {
    data () {
        return {
            par: {
                real_name:'',
                mobile: '',
            },
            name:""

        }
    },
    mounted () {
        this.name =  localStorage.getItem('user_nickname');
        let token = this.$route.query.token;
        let visitor = this.$route.query.visitor;
        if(token != null){
            localStorage.setItem("xx-token",token);
            localStorage.setItem("visitor",visitor);

        }


    },
    methods:{
        doSubmit(){
            if (!this.par.real_name) {
                Toast('请输入你的真实姓名！')
                return false;
            }
            if (!this.par.mobile) {
                Toast('请输入你的手机号！')
                return false;
            }
            this.$http.post(`${this.$root.baseUrl}/api/user/info/beService`, this.par).then(res => {
                Toast(res.data.msg)
                if (res.data.code === 200) {

                    let visitor = localStorage.getItem("visitor");
                    if(visitor == true){
                        window.location.href = "fm://servcie";
                    }else{
                        this.$router.push('/index')
                    }

                }
            }).catch(err => {
                console.log(err)
            })
        }
    }
}





