import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      selected: 1
    }
  },
  watch: {
    '$route': 'watchRoute' // 如果路由有变化，会执行该方法
  },
  methods: {
    watchRoute () {
      if (this.$route.path === '/ordersAll') {
        this.selected = 1
      } else if (this.$route.path === '/ordersHanding') {
        this.selected = 2
      } else if (this.$route.path === '/ordersAwait') {
        this.selected = 3
      }
    }
  }
}



