export default {
    name: "redPacket",
  data () {
      return {
        bonusList: [],
        isNothing: false
      }
  },
  mounted() {
    this.getBonusList()
  },
  methods: {
    getBonusList() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/bonusList`,
        {type: ''}).then(res => {
        if (res.data.code === 200) {
          this.bonusList = res.data.data
          this.isNothing = !this.bonusList.length > 0
        }
      })
    }
  }
}



