import { Toast } from 'mint-ui'
export default {
  name: 'ordersDetail',
  data () {
    return {
      ordersDetail: '',
    }
  },
  mounted () {
    this.getOrdersDetail()
  },
  methods: {
    /**
     * 复制
     * @param val
     */
    doCopy (val) {
      this.$el.querySelector('#copyme').select();
      document.execCommand('copy');
      Toast('复制成功')
    },
    getOrdersDetail () {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/order_list/detail`,
        {
          token: this.$root.token || localStorage.getItem('token'),
          order_id: this.$route.query.orderId
        }).then(res => {
        if (res.data.code === 200) {
          this.ordersDetail = res.data.data
        } else {
          console.log(res.data.msg)
        }
      })
    }
  }
}



