import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import modules from './modules/state'

Vue.use(Vuex)

// 挂载axios到Vue原型对象上
Vue.prototype.$http = axios

const debug = process.env.NODE_ENV !== 'production'

const store = new Vuex.Store({
  modules: {
    modules
  },
  strict: debug
})

export default store



