
import {Toast} from 'mint-ui'
import PicView from '../../components/picView'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import userAvatar from '../../assets/img/defaultShop.png'
export default {
directives: {
TransferDom
},
name: 'shopDetail',
components: {
PicView, XDialog
},
data() {
return {
len: 6,
shopDetail: '',
tabIndex: 0,
defaultImg: 'this.src="' + userAvatar + '"',
productList: [],
commentList: [],
commentTotal: 0,
showDialog: false,
nothing: false,
imgData: [],
pages: {
page: 0
},
isCollect: true
}
},
mounted() {
this.getDetail()
},
computed: {
width() {
let num = 350
return parseInt(num / 37.5) * this.len + 'rem'
}
},
methods: {
/**
* 收藏商品
*/
getCollect() {
let par = {
type: 'collection',
shop_id: this.shopDetail.id,
}
if (!this.isCollect) { // 已收藏过
par.type = 'remove'
this.isCollect = true
} else { //未收藏
par.type = 'collection'
this.isCollect = false
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/detail/shopCollection`,par).then(res => {
Toast(res.data.msg)
if(res.data.code === 200) {
this.isCollect = par.type === 'remove'
}
})
},
/**
* 买单跳转
*/
toPayLink() {
let shop = {
shopId: this.shopDetail.id,
shopName: this.shopDetail.name,
order_type: 1
}
localStorage.setItem('position', this.shopDetail.address)
localStorage.setItem('shop', JSON.stringify(shop))
this.$router.push({path: '/pay', query: {shopId: this.shopDetail.id,shopName: this.shopDetail.name}})
},
getMsgPic(data) {
this.showDialog = data
},
scroll(selector,index) {
this.tabIndex = index
this.$nextTick(() => {
document.body.scrollTop = this.$el.querySelector(selector).offsetTop
})
},
showImgList(list){
this.imgData = list;
this.showDialog=true
},
getDetail() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/detail`,
{
token: this.$root.token || localStorage.getItem('token'),
shop_id: this.$route.query.shopId
}).then(res => {
if (res.data.code === 200) {
this.shopDetail = res.data.data
this.isCollect = this.shopDetail.whetherCollection === 0
this.getShopList()
} else {
Toast(res.data.msg)
}
})
},
getShopList() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/product/Lists`,
{
token: this.$root.token || localStorage.getItem('token'),
shop_id: this.$route.query.shopId,
page: this.pages.page
}).then(res => {
if (res.data.code === 200) {
this.productList = res.data.data
this.getCommentList()
} else {
Toast(res.data.msg)
}
})
},
getCommentList() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/detail/commentList`,
{
shop_id: this.$route.query.shopId,
page: this.pages.page
}).then(res => {
if (res.data.code === 200) {
this.commentList = res.data.data.list;
this.commentTotal = res.data.data.count_all;
this.nothing = this.commentList.length === 0 ? true : false
} else {
Toast(res.data.msg)
}
})
}
}
}



