
import { Toast } from 'mint-ui'
import Icon from 'vux/src/components/icon/index.vue'

export default {
name: 'comment',
components: { Icon },
data() {
return {
par: {
content: '',
price: '',
score: 0,
imgs: '',
user_id: '',
order_id: '',
shop_id: '',
pid: ''
},
ordersDetail: '',
imgList:[],
size: 0,
limit:6,  //限制图片上传的数量
uploadToken: '',
showUrl: 'http://qiniu.xinleapp.com/'
}
},
mounted() {
this.getOrdersDetail()
},
methods: {
getOrdersDetail () {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/order_list/detail`,
{
order_id: this.$route.query.orderId
}).then(res => {
if (res.data.code === 200) {
this.ordersDetail = res.data.data
} else {
console.log(res.data.msg)
}
})
},
delImg(index) {
this.size = this.size - this.imgList[index].size;
this.imgList.splice(index, 1);
if (this.limit !== undefined) this.limit = 6-this.imgList.length;
},
/**
* 上传图片
* @param e
*/
update(e){
let file = e.target.files[0],
param = new FormData();
if(this.imgList.length >= 9){
Toast('上传图片数量不能超过9张');
return false
} else {
param.append('chunk','0');
param.append('chunks','1');
param.append('file',file,file.name)
let config = {
headers:{'Content-Type':'multipart/form-data'}
};

this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/getUploadToken`).then(res => {
this.uploadToken = res.data.data.token
param.append('token',this.uploadToken);
this.uploading(param, config, file.name);
return
})
}
},
/**
* 上传到七牛
*/
uploading(param,config,pathName){
this.$http.post('http://upload.qiniup.com/',param,config).then(res => {
let localArr = this.imgList.map((val,index,arr) => {
return arr[index].localSrc;
})
if(!~localArr.indexOf(pathName)){
this.imgList.push(this.showUrl + res.data.key);
}else{
Toast('已上传该图片');
}
})
},
/**
* 提交评论
* @returns {boolean}
*/
comment() {
if(this.par.score === 0) {
Toast('亲给个评分吧！')
return false
}
if(!this.par.content) {
Toast('评价内容不能为空')
return false
}
let par = {
content: this.par.content,
score: this.par.score,
imgs: this.imgList.join(','),
user_id: this.ordersDetail.uid,
order_id: this.ordersDetail.id,
shop_id: this.ordersDetail.shop_id,
pid: this.ordersDetail.detail[0].product_id
}

this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/order/Shop_order/doOrderComment`,par).then(res => {
Toast(res.data.msg)
if(res.data.code === 200) {
setTimeout(() => {
this.$router.back()
},2000)
}
})
}
}
}



