import { Toast } from 'mint-ui'
export default {
    name: "changeName",
    data() {
      return {
        name: ''
      }
    },
  methods: {
    /**
     * 修改昵称
     * @returns {boolean}
     */
    changeName() {
      console.log(111)
      if(!this.name) {
        Toast('昵称不能为空')
        return false
      }
      let par = {
        id: this.$route.query.id,
        user_nickname: this.name
      }
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/modify`,par).then(res => {
          Toast(res.data.msg)
          setTimeout(() => {
            if(res.data.code === 200) {
              this.$router.back()
            }
          },2500)

      })
    }
  }

}



