export default {
    name: 'changeCity',
    data() {
        return {
            data: [],
            list: [],
            hot_list:[],
        }
    },
    mounted() {
        this.getCityList();
    },
    methods: {
        scroll(selector) {
            this.$nextTick(() => {
                document.body.scrollTop = this.$el.querySelector(selector).offsetTop
            })
        },
        getCityList() {
            let par = {
                keyword: '',
                code: '',
            }
            this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/getCity`, par).then(res => {
                if (res.data.code === 200) {
                    var city_list = res.data.data.city_list;
                    this.hot_list = res.data.data.hot_city_list
                    var tempList = [];
                    for(let i=0;i<city_list.length;i++){
                        if(tempList.indexOf(city_list[i].first_letter) == -1){
                            tempList.push(city_list[i].first_letter);
                        }
                    }
                    for(let i=0;i<tempList.length;i++){
                        var city={};
                        var cityList=[];
                        city.first_letter = tempList[i];
                        for(let a=0;a<city_list.length;a++){
                            if(tempList[i] == city_list[a].first_letter){
                                cityList.push(city_list[a]);
                            }
                        }
                        city.sublist = cityList;
                        this.list.push(city);
                    }
                } else {
                    Toast(res.data.msg)
                }
            })
        }
    }
}



