
import { Toast } from 'mint-ui'
import Countdown from 'vux/src/components/countdown/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'receiveRed',
directives: {
TransferDom
},
components: {
Countdown,Confirm
},
data() {
return {
par:{
username: localStorage.getItem('mobile') === 'undefined' ? '' : localStorage.getItem('mobile'),
password:'',
code:''
},
start: false,
show: false,
time: 50,
tips:{
user:'请输入正确的登录账号',
pwd:'密码错误'
}
}
},
methods: {
onConfirm () {
this.$router.push('/download')
},
finish() {
this.start = false
this.time = 50
},

getCode(){
let status
if (!this.par.username) {
Toast('手机号不能为空! ')
status = true
}
if (this.par.username && !/^(1[3-9])\d{9}$/.test(this.par.username)) {
Toast('手机号格式不正确！')
status = true
}
if(status) {
return false
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/getCode`,{mobile:this.par.username}).then(res => {
Toast(res.data.msg)
if(res.data.code === 200) {
this.start = true
}
}).catch(err => {
console.log(err)
})
},
getLoad() {
let status












if (status) {
return false
}
this.show = true










}
}
}



