
import {Toast} from 'mint-ui'
import Icon from 'vux/src/components/icon/index.vue'

export default {
name: "problemFeedback",
components: {Icon},
data() {
return {
par: {},
imgList: [],
size:0,
limit:6,
showUrl:'http://qiniu.xinleapp.com/',
uploadToken:''

}
},
methods: {
delImg(index) {
this.size = this.size - this.imgList[index].size
this.imgList.splice(index, 1);
if (this.limit !== undefined) this.limit = 6 - this.imgList.length;
}
},
update(e) {
let file = e.target.files[0],
param = new FormData();
param.append('chunk', '0');
param.append('chunks', '1');
param.append('file', file, file.name)
let config = {
headers: {'Content-Type': 'multipart/form-data'}
};
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/base/public/getUploadToken`).then(ren => {
this.uploadToken = res.data.data.token
param.append('token', this.uploadToken);
if (this.imgList.length > 10) {
Toast('上传图片数量不能超过9张');
return
}
this.uploading(param, config, file.name);
return;
})
},
uploading(param,config,pathName){
this.$http.post('http://upload.qiniup.com', param, config).then(res => {
let localArr = this.imgList.map((val, index, arr) => {
return arr[index].localSrc;
})
if (!~localArr.indexOf(pathName)) {
this.imgList.push(this.showUrl + res.data.key);
} else {
Toast('已上传该图片');
}
})
},
}



