
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import defaultImg from '../../assets/img/defaultShop.png'
export default {
components: {
InlineLoading,Scroller,LoadMore, Divider
},
data() {
return {
topStatus: '',
defaultImg: 'this.src="' + defaultImg + '"',
nothing: false,
distanceLab:"附近",
sortLab:"智能排序",
sortValue:"",
typeValue:this.$route.query.id,
distanceValue:"3000",
typeLab: "",
typeData: [],
tempTypeData:[], //类型
sortData: [],  //排序
shopList:[],
distanceData: [], //附近
selectData: [],  // tab选中
selected: 1,
liItem: 0,
currentSelectItem: "fine",
isShow: false,
page:0,
selectType: '',
selectIndex: 0,
onFetching: false,
bottomAllLoaded:true,
isClass: false,
status: false
}
},
mounted() {
var subName = this.$route.query.subName;
if(subName != null){
this.typeLab = subName;
this.typeValue = this.$route.query.subId;
}else{
this.typeLab = this.$route.query.name
this.typeValue = this.$route.query.id;
}
this.getCateList();
this.getShopList(this.typeLab ,-1);
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
})
},
methods: {
loadTop() {
if(!this.selectType && this.selectIndex === 0) {
this.getShopList(this.typeLab ,-1,'up');
} else if(this.selectType){
this.getShopList(this.selectType,this.selectIndex,'up');
}
},
/**
* 加载更多
*/
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true
setTimeout(() => {
this.page += 1
if(!this.selectType && this.selectIndex === 0) {
this.getShopList(this.typeLab ,-1);
} else if(this.selectType){
this.getShopList(this.selectType,this.selectIndex);
}
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
this.onFetching = false
}, 2500)

}
},
/**
* 切换tab
*/
changeTab(val, type) {
this.liItem = this.selected === val ? this.liItem : 0
this.selected = val
this.isShow = true;
this.page = 0;
if (type === 'fine') {
this.currentSelectItem = "fine";
this.selectData = this.tempTypeData;

} else if (type === 'nearby') {
this.currentSelectItem = "nearby";
this.distanceData[0] = "1km";
this.distanceData[1] = "3km";
this.distanceData[2] = "5km";
this.distanceData[3] = "8km";
this.distanceData[4] = "10km";
this.distanceData[5] = "全城";
this.selectData = this.distanceData;
} else if (type === 'sort') {
this.currentSelectItem = "sort";
this.sortData[0] = "智能排序";
this.sortData[1] = "人气优先";
this.sortData[2] = "人均从高到低";
this.sortData[3] = "人均从低到高";
this.selectData = this.sortData;
}
},
/**
* 点击 tab 里的 li
*/
getParams(item, position) {
this.selectType = item
this.selectIndex = position
this.shopList.splice(0,this.shopList.length)
this.getShopList(item,position)
},
/**
* 筛选 food 列表
* @param item
*/
getShopList(item,position,type) {
this.isClass = false
this.liItem = position === -1 ? 0 : position
if(this.currentSelectItem == "fine"){
this.typeLab = item;
if(position != -1){
this.typeValue = this.typeData[position].id;
}
}else if(this.currentSelectItem == "nearby"){
this.distanceLab = item;
if(position != -1){
if(position == 0){
this.distanceValue = "1000";
}else if(position == 1){
this.distanceValue = "3000";
}else if(position == 2){
this.distanceValue = "5000";
}else if(position == 3){
this.distanceValue = "8000";
}else if(position == 4){
this.distanceValue = "10000";
}else if(position == 5){
this.distanceValue = "100000";
}
}
}else if(this.currentSelectItem == "sort"){
this.sortLab = item;
if(position != -1){
if(position == 0){
this.sortValue = "";
}else if(position == 1){
this.sortValue = "1";
}else if(position == 2){
this.sortValue = "3";
}else if(position == 3){
this.sortValue = "2";
}
}
}
this.isShow = false;
let par = {
cate_id: this.typeValue,
distance:this.distanceValue,
page:this.page,
city_code: this.$root.address.city_code,
lng: this.$root.lng,
lat: this.$root.lat,
sort:this.sortValue
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/home/nearby/ShopList`, par).then(res => {
if (res.data.code === 200) {
if (!this.shopList && this.selectType || (type && type === 'up')) {
this.shopList = res.data.data
} else {  // 加载更多的时候才遍历
for (let i in res.data.data) {
this.shopList.push(res.data.data[i])
}
}
setTimeout(() => {
this.isClass = true
},2000)
this.onFetching = res.data.data.length >= 15 ? false : true
this.status = res.data.data.length >= 15 ? false : true
this.nothing = !this.shopList.length > 0
}
})
},
/**
* 获取子类型列表
*/
getCateList() {
let par = {
parent_id: this.$route.query.id
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/shop/category`, par).then(res => {
if (res.data.code === 200) {

var dataObj = {};
dataObj.name = this.$route.query.name;
dataObj.id = this.$route.query.id;
this.typeData.push(dataObj);
for(let i=0;i<res.data.data.length;i++){
this.typeData.push(res.data.data[i]);
}
for(let i=0;i<this.typeData.length;i++){
this.tempTypeData.push(this.typeData[i].name);
}
this.selectData = this.tempTypeData;

}
})
}
}
}



