
import { Toast } from 'mint-ui'
import Icon from 'vux/src/components/icon/index.vue'

export default {
components: { Icon },
data () {
return {
type: 'password',
par: {
username: '',
password: '',
ref_mobile: '',
code: '',
device_type: 'web'
},
tips: {
user: '请输入正确的登录帐号',
pwd: '密码错误'
}
}
},
mounted () {

},
methods: {

changeType () {
if (this.type === 'password') {
this.type = 'text'
} else {
this.type = 'password'
}
},

getCode () {
let status
if (!this.par.username) {
Toast('手机号不能为空！')
status = true
}
if (status) {
return false
}
this.$http.post(`${this.$root.baseUrl}/api/base/public/getCode`, {mobile: this.par.username}).then(res => {
Toast(res.data.msg)
if (res.code === 200) {
}
}).catch(err => {
console.log(err)
})
},
/**
* 注册用户
* @returns {boolean}
*/
getLoad () {
let status
if (!this.par.username) {
Toast('手机号不能为空！')
status = true
}




if (this.par.ref_mobile && !/^(1[3-9])\d{9}$/.test(this.par.ref_mobile)) {
Toast('推荐人手机号格式不正确！')
status = true
}
if (this.par.username && !/^(1[3-9])\d{9}$/.test(this.par.username)) {
Toast('手机号格式不正确！')
status = true
}
if (status) {
return false
}
this.$http.post(`${this.$root.baseUrl}/api/user/public/register`, this.par).then(res => {
Toast(res.data.msg)
if (res.data.code === 200) {
this.$root.token = res.data.data.token
this.$root.mobile = res.data.data.mobile
this.$root.sessid = res.data.data.sessid
this.$root.user_nickname = res.data.data.user_nickname
localStorage.setItem('mobile', res.data.data.mobile)
this.$router.push('/index')
}
}).catch(err => {
console.log(err)
})
}
}
}



