
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
name: 'voucherMore',
components: {
InlineLoading
},
data() {
return {
status: false,
loading: false,
isShow: false,
shopId: this.$route.query.shopId,
couponList: [],
page: 0
}
},
mounted() {
this.getVoucherList()
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.page += 1
this.getHomeShop()
},2500)
} else {
this.loading = false
}
},
getVoucherList() {
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/merchant/coupon`,
{
shop_id: this.shopId,
page: this.page,
type: 'user'
}).then(res => {
if (res.data.code === 200) {
if(!this.couponList) {
this.couponList = res.data.data
} else {
for (let i in res.data.data) {
this.couponList.push(res.data.data[i])
}
}
this.status = res.data.data.length < 15 ? false : true
this.loading = false
this.isShow = !this.couponList.length
}
})
}
}
}



