export default {
  name: 'voucherList',
  data () {
    return {
      selected: 1,
      dateTime: new Date().getTime(),
      pasted: true,
      couponList: [],
      couponUse: [],
      couponUnused: [],
      page: 0,
      isNothing1: false,
      isNothing2: false,
      isNothing3: false
    }
  },
  filters: {
    /**
     * 代金券是否过期
     * @param val
     * @param timestamp
     * @returns {string}
     */
    formatType(val) {
      let str = ''
      if(!val && val !== 0) return str = ''
      if (val === 1) {
        str = '已使用'
      } else if (val === 0) {
        str = '未使用'
      } else if (val === 2) {
        str = '已过期'
      }
      return str
    }
  },
  mounted() {
      this.getVoucherList()
  },
  methods: {
      getVoucherList() {
        this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/coupon`,
          { status: 'all', page: this.page }).then(res => {
          if (res.data.code === 200) {
              this.couponList = res.data.data
              this.isNothing1 = !this.couponList.length > 0
              this.getVoucherUnuse()
              this.getVoucherUnused()
          }
        })
      },
      getVoucherUnuse() {
        this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/coupon`,
          { status: 0, page: this.page }).then(res => {
          if (res.data.code === 200) {
            this.couponUse = res.data.data
            this.isNothing2 = !this.couponUse.length > 0
          }
        })
      },
      getVoucherUnused() {
      this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/coupon`,
        { status: 1, page: this.page }).then(res => {
        if (res.data.code === 200) {
          this.couponUnused = res.data.data
          this.isNothing3 = !this.couponUnused.length > 0
        }
      })
    }
  }
}



