
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
name: 'withdraw',
components:{
InlineLoading
},
data() {
return {
status: false,
loading: false,
totalData: [],
isShow: false,
pages: {
page: 0
}
}
},
mounted() {
this.getAccount ()
},
methods: {
/**
* 加载更多
*/
loadMore() {
if (this.status) {
this.loading = true
setTimeout(() => {
this.page += 1
this.getAccount()
},2500)
} else {
this.loading = false
}
},
getAccount () {
let par = {
type: 'withdraw',
page: this.pages.page
}
this.$http.post(`${window.SITE_CONFIG['baseURL']}/api/user/info/accountLog`,par).then(res => {
if(res.data.code === 200) {
if (!this.totalData) {
this.totalData = res.data.data
} else {
for (let i in res.data.data) {
this.totalData.push(res.data.data[i])
}
if (res.data.data.length < 15) {
this.status = false
} else if (res.data.data && res.data.data.length === 15){
this.status = true
}
}
this.loading = false
if(!this.totalData.length) {
this.isShow = true
}
} else {
Toast(res.data.msg)
}
})
}
}
}



